module.exports = registerEndpoints;

function registerEndpoints(octokit, routes) {
  Object.keys(routes).forEach(function (namespaceName) {
    if (!octokit[namespaceName]) {
      octokit[namespaceName] = {};
    }

    Object.keys(routes[namespaceName]).forEach(function (apiName) {
      var apiOptions = routes[namespaceName][apiName];
      var endpointDefaults = ['method', 'url', 'headers'].reduce(function (map, key) {
        if (typeof apiOptions[key] !== 'undefined') {
          map[key] = apiOptions[key];
        }

        return map;
      }, {});
      endpointDefaults.request = {
        validate: apiOptions.params
      };
      var request = octokit.request.defaults(endpointDefaults);

      if (apiOptions.deprecated) {
        octokit[namespaceName][apiName] = function () {
          octokit.log.warn(apiOptions.deprecated);
          octokit[namespaceName][apiName] = request;
          return request.apply(null, arguments);
        };

        return;
      }

      octokit[namespaceName][apiName] = request;
    });
  });
}