var endpoint = require('@octokit/endpoint');

var getUserAgent = require('universal-user-agent');

var version = require('./package.json').version;

var userAgent = "octokit-request.js/".concat(version, " ").concat(getUserAgent());

var withDefaults = require('./lib/with-defaults');

module.exports = withDefaults(endpoint, {
  headers: {
    'user-agent': userAgent
  }
});